.footer {
  position: relative;
  margin-top: -105px;
  /* top: -98px; */
  background-color: var(--green);
  color: var(--white);
}

.footer__wrapper {
  display: grid;
  grid-template-columns: 432px 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 7%;
  padding-top: 178px;
  padding-bottom: 80px;
}

.footer__subtitle {
  font-size: 20px;
  line-height: 24px;
  text-align: left;
  color: var(--grey);
  margin-bottom: 20px;
}

.footer__discription {
  font-family: Nunito Sans;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-align: left;
  margin-bottom: 80px;
}

.footer__contacts {
  display: flex;
  gap: 24px;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  text-align: left;
  text-decoration: underline;
  margin-bottom: 32px;
}

.footer__social {
  display: flex;
  gap: 18px;
  align-items: center;
}

.footer__center {
  max-width: 280px;
}

.footer__item + .footer__item {
  margin-top: 16px;
}

.footer__item {
  padding-left: 8px;
}

.footer__item::before {
  content: "";
  border: solid #fff;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 3px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  position: relative;
  left: -8px;
  transition: all 0.3s ease;
}

.footer__item:hover::before {
  border: solid var(--orange);
  border-width: 0 2px 2px 0;
}

.footer__item:nth-child(5):after {
  content: "new";
  padding: 2px 10px;
  background: #ffffff;
  border-radius: 4px;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  text-align: left;
  color: var(--green);
  margin-left: 10px;
}

.footer__link {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: left;
  transition: all 0.3s ease;
}

.footer__link:hover {
  position: relative;
  color: var(--orange);
  text-decoration: underline;
}

.footer__right {
  max-width: 280px;
}

@media (max-width: 1120px) {
  .footer__wrapper {
    padding-left: 5%;
    padding-right: 5%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    justify-content: space-between;
    grid-template-areas: "left left" "center right";
  }
  .footer__left {
    grid-area: left;
  }
  .footer__center {
    grid-area: center;
  }
  .footer__right {
    grid-area: right;
  }
}
@media (max-width: 578px) {
  .footer__wrapper {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas: "left" "center" "right";
  }
  .footer__discription {
    margin-bottom: 30px;
  }
  .footer__right {
    margin-bottom: 50px;
  }
}
